.contacts {
  @media (min-width: $desktop-width) {
    position: relative;
    padding: 97px 0 105px;
  }
}

.contacts--bg {
  background-color: $basic_grey;
}

.contacts__wrapper {
  @media (min-width: $desktop-width) {
    position: relative;
    max-width: 1440px;
    width: 100%;
    margin-left: auto;
    z-index: 100;
  }
}

.contacts__content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 27px;
  padding-bottom: 27px;

  @media (min-width: $tablet-width) {
    padding: 0;
    padding-left: 41px;
    padding-right: 41px;
    padding-bottom: 63px;
  }

  @media (min-width: $desktop-width) {
    width: 570px;
    left: 110px;
    top: 95px;
    padding: 66px 88px 61px 77px;

    background-color: $basic-white;
  }
}

.contacts__content--catalog {
  padding-top: 27px;

  @media (min-width: $tablet-width) {
    padding-top: 0;
  }

  @media (min-width: $desktop-width) {
    padding-top: 65px;
    padding-bottom: 63px;
  }
}

.contacts__invite-text {
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: $special-black;

  margin: 0;
  padding: 0;
  width: 140px;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 24px;

    width: 171px;
    padding-top: 66px;
  }

  @media (min-width: $desktop-width) {
    padding-top: 0;

    text-align: left;
  }
}

.contacts__address-text {
  font-family: $add-font;
  font-size: 14px;
  line-height: 20px;
  color: $special_dark-grey;

  margin: 0;
  padding: 0;
  width: 140px;
  align-self: flex-start;

  @media (min-width: $tablet-width) {
    width: 159px;
    text-align: right;
    padding-top: 63px;

    font-size: 16px;
    line-height: 20px;
  }

  @media (min-width: $desktop-width) {
    text-align: left;

    padding-top: 0;
    padding-bottom: 13px;
  }
}

.contacts__map-container {
  display: flex;
}

.contacts__map {
  height: 362px;

  background-image: url("../../img/map-mobile.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  @media (min-resolution: 2dppx) {
    background-image: url("../../img/map-mobile@2x.jpg");
  }

  @media (min-width: $tablet-width) {
    height: 400px;

    background-image: url("../../img/map-tablet.jpg");

    @media (min-resolution: 2dppx) {
      background-image: url("../../img/map-tablet@2x.jpg");
    }
  }

  @media (min-width: $desktop-width) {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;

    background-image: url("../../img/map-desktop.jpg");

    @media (min-resolution: 2dppx) {
      background-image: url("../../img/map-desktop@2x.jpg");
    }
  }
}
